@tailwind base;
@tailwind components;
@tailwind utilities;

.fadeGroup-enter-active {
  animation: bounce-in .5s;
}
.fadeGroup-leave-active {
  display: none;
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}